"use client";

import { Button } from "@/components/ui/button";
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetTrigger } from "@/components/ui/sheet";
import { Menu } from "lucide-react";
import Image from "next/image";
import { useEffect, useState } from "react";

const Navbar = () => {
  const [open, setOpen] = useState(false);
  const [isScrolling, setIsScrolling] = useState(false);

  // Bloqueia o scroll quando o menu está aberto
  useEffect(() => {
    if (open) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
    return () => {
      document.body.style.overflow = '';
    };
  }, [open]);

  const handleNavigation = (id: string, e: React.MouseEvent) => {
    e.preventDefault();
    setIsScrolling(true);
    
    // Fecha o menu primeiro
    setOpen(false);
    
    // Aguarda a animação de fechamento do menu
    setTimeout(() => {
      const element = document.getElementById(id);
      if (element) {
        element.scrollIntoView({ behavior: "smooth" });
        
        // Atualiza a URL sem causar refresh
        window.history.replaceState({}, "", `#${id}`);
      }
      setIsScrolling(false);
    }, 300); // Tempo correspondente à animação de fechamento do Sheet
  };

  return (
    <header className="flex justify-between p-3.5 md:p-11">
      <Image
        src="/images/Logo.svg"
        alt="Logo da LivroSaaS"
        width={129}
        height={40}
      />

      <nav className="hidden md:block">
        <ul className="flex items-center gap-6">
          <li>
            <a 
              href="#funcionamento" 
              className="hover:underline"
              onClick={(e) => handleNavigation("funcionamento", e)}
            >
              Funcionamento
            </a>
          </li>
          <li>
            <a 
              href="#preço" 
              className="hover:underline"
              
            >
              Preço
            </a>
          </li>
          <li>
            <Button variant="secondary" className="border border-gray-400">
              Login
            </Button>
          </li>
        </ul>
      </nav>

      <Sheet open={open} onOpenChange={setOpen}>
        <SheetTrigger asChild>
          <Button variant="ghost" size="icon" className="md:hidden">
            <Menu />
          </Button>
        </SheetTrigger>
        <SheetContent side="right">
          <SheetHeader>
            <SheetTitle className="text-center">Menu</SheetTitle>
          </SheetHeader>
          <ul className="flex flex-col items-center gap-4 space-y-6 mt-24">
            <li>
              <a
                href="#funcionamento"
                className="hover:underline"
                onClick={(e) => handleNavigation("funcionamento", e)}
              >
                Funcionamento
              </a>
            </li>
            <li>
              <a
                href="#preço"
                className="hover:underline"
                onClick={(e) => handleNavigation("preço", e)}
              >
                Preço
              </a>
            </li>
            <li>
              <Button
                variant="secondary"
                className="border border-gray-400"
                onClick={() => setOpen(false)}
              >
                Login
              </Button>
            </li>
          </ul>
        </SheetContent>
      </Sheet>
    </header>
  );
};

export default Navbar;
